.social-media {
    position: fixed;
    z-index: 100;
    top: 50%;
    right: 0;

}

.social-links {
  cursor: pointer;
 
}

.facebook , .instagram , .linkedin , .email {
    display: block;
   color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;

}
.facebook {
    background-color: #3b5998;
}
.instagram {
    background-color: #e1306c;
}
.linkedin{
    background-color: #0077b5;
}.email {
    background-color: #ea4335;
}

.social-links :hover {
    opacity: 0.8;
    transition: opacity 0.3s ease;
  
}

.socal-order {
  position: fixed;
    z-index: 100;
    top: 80%;
    right: 8px;
}


   


.chat , .whats-app {
 display: block;
   color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    font-size: 30px;
   


}
.chat {
 display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: #002129;
    border-radius:50%;
}
 .whats-app {
   margin-top: 15px;
 display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: #002129;
    border-radius:50%;
    
 }